.nav {
    margin-top: 4rem;
    width: 100%;
    height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    
}

.nav div {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    list-style-type: none;
    background-color: white;
    border-radius: 10px;
    height: 300px;
    width: 90%;
    box-shadow: 0 1px 1px rgb(228, 227, 227);
}

.nav div li {
    /* margin: .5rem 0; */
    color: black;
    padding: 1rem;
    border-bottom: 0.1px solid rgb(225, 223, 223);
    cursor: pointer;
    font-weight: 500;
    font-size: 17px;
}

.nav div li:last-of-type {
    border: none;
}


.nav::after{
    height: 100vh;
    width: 100vw;
    background-color: var(--Darkgray);
    opacity: .4;
    content: "";
    position: absolute;
    top: 155px;
}

@media screen and (min-width: 800px) {
    .nav{
        display: none;
    }
}