.card,
.cardSelected {
    background-color: white;
    margin: 1rem 0;
    border-radius: 15px;
    padding: 2rem;
    box-sizing: border-box;
    border: 2px solid rgb(214, 213, 213);
}

.cardSelected {
    border: 2px solid var(--Dark-cyan);
}

.check {
    height: 20px;
    width: 20px;
    border: 1px solid grey;
    border-radius: 50%;
    display: grid;
    place-content: center;
    cursor: pointer;
    margin-right: 10px;
}

.checkInner {
    height: 12px;
    width: 12px;
    background-color: var(--Dark-cyan);
    border-radius: 50%;
}

.top {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.top h2 {
    font-size: 15px;
    margin-bottom: 5px;
}

.top h3 {
    margin-top: 0;
    font-size: 14px;
    color: var(--moderate-cyan);
}

.card h5,
.cardSelected h5 {
    color: black;
    font-size: 18px;
    display: inline;
}

.bottom {
    border-top: 1px solid rgb(214, 213, 213);
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
}

.bottom div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.dollar {
    position: absolute;
    left: 20px;
    top: 12px;
    font-size: 16px;
    /* z-index: 900; */
}

input {
    height: 40px;
    width: 100px;
    border-radius: 20px;
    box-sizing: border-box;
    border: 1px solid rgb(214, 213, 213);
    outline: none;
    position: relative;
    padding-left: 35px;
    display: flex;
    align-items: center;
    color: black;
    font-size: 18px;
    font-weight: bold;
}

button {
    border-radius: 20px;
    margin-left: 5px;
    height: 40px;
    width: 100px;
    border: none;
    color: white;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    background-color: var(--Dark-cyan);
}

p {
    margin-bottom: 10px;
}

.error {
    border: 1px solid red;
}

.empty {
    margin-top: 10px;
    align-self: flex-start;
}
@media screen and (min-width: 800px) {
    .second {
        flex: 2;
        display: flex;
        /* justify-content: space-around; */
        align-items: center;
    }

    .top h2 {
        font-size: 15px;
        margin-right: 10px;
        margin-bottom: 0;
    }

    .top h3 {
        margin-top: 0;
        font-size: 14px;
        color: var(--moderate-cyan);
    }
    .bottom {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding-top: 20px;
    }

    p {
        padding-bottom: 10px;
    }
    .bottom p {
        padding: 0;
        margin: 0;
    }
    .bottom div {
        padding-top: 5px;
        position: relative;
    }
    .dollar{
        top: 16px;
    }
}
