.btn, .btnop {
    color: white;
    background-color: var(--moderate-cyan);
    height: 40px;
    padding: 1.2rem;
    border-radius: 25px;
    width: 140px;
    box-sizing: border-box;
    display: grid;
    place-content: center;
}

.btnop {
    background-color: grey;
    cursor: not-allowed;
}

.btnlg{
    color: white;
    background-color: var(--moderate-cyan);
    border-radius: 40px;
    height: 60px;
    width: 150px;
    display: grid;
    place-content: center;
    padding: 1.5rem .9rem;
    box-sizing: border-box;
}

.btn, .btnlg{
    cursor: pointer;
}
