.card, .cardblured {
    padding: 30px 20px;
    border-radius: 20px;
    border: 0.1rem solid rgb(228, 227, 227);
    margin: 2rem 0;
    box-shadow: 0 1px 1px rgb(228, 227, 227);
}

.cardblured{
    opacity: .5;
}

.secondary {
    color: var(--moderate-cyan);
    margin-top: 10px;
}

.card h3, .cardblured h3 {
    margin: 0;
}

.card h2, .cardblured h2 {
    padding: 0;
    margin: 0;
    font-size: 25px;
    margin-right: 5px;
}

.bottom div {
    display: flex;
    align-items: center;
    /* flex: 1; */
    margin: 20px 0;
}

@media only screen and (min-width: 800px) {
    .bottom {
        margin-top: 20px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}
