:root {
    --moderate-cyan: hsl(176, 50%, 47%);
    --Dark-cyan: hsl(176, 72%, 28%);

    --Black: hsl(0, 0%, 0%);
    --Darkgray: hsl(0, 0%, 48%);
}

* {
    margin: 0;
    padding: 0;
}

body {
    background-color: rgb(247, 247, 247);
    font-family: sans-serif;
    color: var(--Darkgray);
    font-size: 15px;
    scrollbar-width: none;
    line-height: 23px;
}

body::-webkit-scrollbar{
    display: none;
}

.hide-mobile {
    display: none;
}

.hide-desktop {
    display: block;
}



.main {
    width: 90%;
    max-width: 600px;
    margin: -3.5rem auto 0;
    z-index: -1;
}



@media screen and (min-width: 800px) {
    .hide-mobile {
        display: block;
    }

    .hide-desktop {
        display: none;
    }
}
