.stat{
    text-align: center;
    margin: 1rem 0 1.5rem;
}

.primary{
    color: black;
    font-weight: bolder;
    font-size: 25px;
}

.secondary {
    margin-bottom: 1rem;
}


@media screen and (min-width: 800px){
    .stat {
        border-right: 1px solid var(--Darkgray);
        text-align: left;
        padding: 0 2rem;

    }

    .stat:last-of-type{
        border: none;
    }
}