.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: .5rem 0  2rem;
}

.header h2{
    display: inline-block;
    margin: 0;padding: 0;
    font-size: 17px;
}

.modal{
    padding: 2rem 20px;
    background-color: #fff;
    border-radius: 15px;
    position: absolute;
    width: 75%;
    /* left: 20%; */
    max-width: 560px;
    margin: auto;
    top: 40%;
    /* z-index: 810; */
}

.modalBefore{
    height: 100vh;
    position: fixed;
    width: 100vw;
    content: "";
    background-color: rgb(228, 228, 228);
    top: 0;
    left: 0;
    opacity: .4;
    /* z-index: 70; */
}

.icon{
    cursor: pointer;
}

.icon:hover{
    fill: black;
    color: black;
}