.bookmark {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.icon {
    flex: 1;
}

.bookmark span {
    display: none;
}
.mastercraft {
    margin-top: -4rem;
    margin-bottom: 2rem;
}

.bookmarkcard {
    width: 100%;
    background-color: #fff;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 1px 18px rgb(170, 170, 170);
    box-sizing: border-box;
    border-radius: 15px;
    text-align: center;
}

.firstoftype {
    width: 100%;
    margin-top: 2rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

h2 {
    margin-bottom: 1rem;
    color: black;
    font-weight: bolder;
}

@media screen and (min-width: 800px) {
    .bookmark {
        width: 170px;
        border-radius: 40px;
        background-color: rgb(218, 213, 213);
    }

    .bookmark span {
        display: block;
        height: 100%;
        font-size: 16px;
        font-weight: bold;
        padding-left: -2rem;
        flex: 2;
        color: black;
    }
}
