.modal{
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: grey;
    opacity: .5;
    top: 0;
    left: 0;
}

.completed {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    display: grid; 
    place-content: center;
    box-sizing: border-box;
    padding: 0 20px;
    /* z-index: 8999; */
 
    
}

.completed div{
    background-color: white;
    /* width: 70%; */
    max-width: 350px;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 20px;
    text-align: center;
}

.completed h2{
    margin-top: 20px;
    margin-bottom: 0;
}

.completed button {
    padding: .5rem 1rem;
    font-size: 18px;
}

.completed p{
    margin: 10px 0 20px;
    text-align: center;
    line-height: 20px;
}