.container {
    width: 50%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.outer {
    height: 15px;
    width: 80%;
    border-radius: 20px;
    background-color: rgb(230, 229, 229);
}

.inner {
    height: 100%;
    width: 80%;
    border-radius: 20px;
    background-color: var(--moderate-cyan);
}

.stat {
    margin-top: 2rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: white;
    /* box-shadow: 0px 6px 17px var(--Darkgray); */
    margin-bottom: 2rem;
    border-radius: 15px;
    padding: 2rem 0;
}

hr {
    width: 50%;
}


@media screen and (min-width: 800px) {
    hr {
        display: none;
    }

    .container{
        width: 80%;
        flex-direction: row;
        justify-content: flex-start;
        text-align: left;
        margin-bottom: 2rem;
    }
}